import { SET_ALERT, REMOVE_ALERT } from '../actions/types';
const initialState = [];

export default function (state = initialState, action) {
  const { type, payload } = action; /*Destructured it */
  switch (type /*For type */) {
    case SET_ALERT:
      return [
        ...state,
        payload,
      ]; 
    case REMOVE_ALERT:
      return state.filter(
        (alert) =>
          alert.id !==
          payload 
           );
    default:
      return state;
  }
}
